import React from "react";
import "./services.css";
import Hypothesis from "../HypothesisV2.png";
import Build from "../BuildV2.png";
import Measure from "../MeasureV2.png";
import Learn from "../LearnV2.png";
import Repeat from "../RepeatV2.png";
import { faRubleSign } from "@fortawesome/free-solid-svg-icons";

class Services extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className='servicesPage' id='OurApproach'>
        <div className="processTitle"><h1>OUR PROMISES</h1></div>
        <div className="serviceOverview">
          <div className="services"><img src={Hypothesis}></img><h4 style={{borderColor: "rgb(47,118,151)"}}>MOONSHOTS</h4><p>We enable you to identify promising opportunities to expand existing brands or build completely new ones.
            </p></div>
          <div className="services"><img src={Build}></img><h4 style={{borderColor: "rgb(255,39,100)"}}>BUILD</h4><p>We build the ideal marketing materials and prototypes to quickly validate some critical assumptions and de-risk your vision.
</p></div>
          <div className="services"><img src={Measure}></img><h4 style={{borderColor: "rgb(84,217,202)"}}>MEASURE</h4><p>We empirically determine your concepts' desirability and viability through well-designed qualitative and quantitative user tests.
</p></div>
          <div className="services"><img src={Learn}></img><h4 style={{borderColor: "rgb(32,53,95)"}}>LEARN</h4><p> We provide you with clear, empirically-driven insights to increase the desired product-market fit.
</p></div>
          <div className="services"><img src={Repeat}></img><h4 style={{borderColor: "rgb(47,118,151)"}}>REPEAT</h4><p>We only stop iterating when we’ve developed a unique marble that will make your organization fly even higher*.
</p></div>
        </div>
          <p className="peterPanReference">* This reference makes more sense when you're a fan of Peter Pan (which we are ;)</p>
      </div>
    );
  }
}

export default Services;
